
.cp-index {
  width: 100%;
  height: 100%;
  padding-top: 1.875rem;
  background-image: url("~assets/test.jpg");
  background-size: cover;
  background-position: center center;
}
.cp-index h1 {
    margin: auto;
    width: 21.875rem;
    height: 3.125rem;
    line-height: 3.125rem;
    font-size: 1.875rem;
    text-align: center;
    background-image: -webkit-linear-gradient(left top, #F00, #F60, #FF0, #0C0, #699, #06C, #909);
    background-image: linear-gradient(to right bottom, #F00, #F60, #FF0, #0C0, #699, #06C, #909);
    background-size: 100% 100%;
    color: transparent;
    -webkit-background-clip: text;
}



