
* {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
html * {
  outline: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
html, body, div, span, small, p, em, i, b, th, td, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, form, textarea, input {
  padding: 0;
  margin: 0;
}
a {
  color: #9fa0a0;
  text-decoration: none;
}
a:active, a:visited {
    color: currentColor;
}
a,
img {
  border: none;
  touch-callout: none;
}
ul,
ol {
  list-style: none;
}
i,
em {
  font-style: normal;
}
s {
  text-decoration: none;
}
table {
  border-spacing: 0;
  border-collapse: collapse;
}
input:focus,
select:focus,
textarea:focus,
select,
button {
  margin: 0;
  outline: none;
}
input,
button {
  background-image: none;
}
@-webkit-keyframes popIn {
0% {
    opacity: 0;
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%);
    -webkit-filter: blur(2px);
            filter: blur(2px);
}
100% {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    -webkit-filter: blur(0);
            filter: blur(0);
}
}
@keyframes popIn {
0% {
    opacity: 0;
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%);
    -webkit-filter: blur(2px);
            filter: blur(2px);
}
100% {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    -webkit-filter: blur(0);
            filter: blur(0);
}
}
html, body {
  width: 100%;
  height: 100%;
}
#app {
  width: 100%;
  height: 100%;
}



